import axios from "axios";

// 

// get
function axiosGet(path) {
  return axios.get(path).then((res) => res.data);
}

// post
function axiosPost(path, data) {
  return axios.post(path, data).then((res) => res.data);
}

// 
function submitFile(relativePath, newContent) {
  return axios
    .post(`/submit`, { relativePath, newContent })
    .then((res) => res.data);
}

// 
function deleteFiles(files) {
  return axios.post("/delete", { files }).then((res) => res.data);
}

// 
function renameFile(relativePath, originName, newName) {
  return axios
    .post(`/rename`, { relativePath, originName, newName })
    .then((res) => res.data);
}

// 
function addFile(relativePath, fileName) {
  return axios
    .post(`/createFile`, { relativePath, fileName })
    .then((res) => res.data);
}

// 
function moveFile(shortName, srcRelativePath) {
  return axios
    .post(`/recoverItem`, { shortName, srcRelativePath })
    .then((res) => res.data);
}

