// 
function uploadFile(progress, formData) {
  let config = {
    headers: { "Content-Type": "multipart/form-data" },
    onUploadProgress: function (e) {
      //lengthComputable
      //lengthComputablefalsee.totale.loaded
      if (e.lengthComputable) {
        var rate = e.loaded / e.total; //
        progress.style.backgroundImage = `linear-gradient(to right, rgba(9, 170, 255, .5) ${(
          rate * 100
        ).toFixed(2)}%, white 0, white 100%)`;
      }
    },
  };
  return axios.post("/upload", formData, config).then((res) => res.data);
}

// 
function copyFile(fileName, srcRelativePath, targetRelativePath) {
  return axios
    .post("/copyFile", { fileName, srcRelativePath, targetRelativePath })
    .then((res) => res.data);
}

// 
function addDir(relativePath, folderName) {
  return axios
    .post(`/createFolder`, { relativePath, folderName })
    .then((res) => res.data);
}

// 
function low(func, time) {
  let flag = true;
  return function (...args) {
    if (flag) {
      flag = false;
      setTimeout(() => {
        flag = true;
      }, time);
      return func.call(this, ...args);
    } else {
      return Promise.reject();
    }
  };
}

// 
function delay(func, time) {
  let id;
  return function (...args) {
    clearTimeout(id);
    id = setTimeout(() => {
      func.call(this, ...args);
    }, time);
  };
}

// 
function login(config) {
  return axios.post("/login", { ...config }).then((res) => res.data);
}

// 
function logout() {
  return axios.post("/logout").then((res) => res.data);
}

// 
function autoLogin() {
  return axios.get("/autoLogin").then((res) => res.data);
}

// 
function register(config) {
  return axios.post("/register", { ...config }).then((res) => res.data);
}

export {
  axiosGet,
  axiosPost,
  submitFile,
  renameFile,
  moveFile,
  addFile,
  deleteFiles,
  downloadFile,
  addDir,
  copyFile,
  uploadFile,
  low,
  delay,
  login,
  logout,
  autoLogin,
  register,
};
