import { createStore, applyMiddleware } from "redux";
import mime from "mime-types";
import { reducerFunction } from "./actionFunc";
import { axiosPost } from "../tools/requestData";

// storereducer
function listFilterReducer(state, action) {
  let handler = reducerFunction[action.type];
  if (handler) {
    try {
      let re = handler(state, action);
      return re;
    } catch (e) {
      return state;
    }
  } else {
    return state;
  }
}

// 
export default createStore(
  listFilterReducer,
  {
    // 
    isLogin: false,
    // 
    loginUsername: "",
    // 
    fileModel: true,
    // file
    fileModelType: "table",
    // 
    filterName: "filterDefault",
    // echarts
    echartsModel: false,
    // echarts
    echartsModelType: "pie",
    // 
    recoverModel: false,
    // 
    allowSize: 1000000000,
    // 
    totalSize: 0,
    // 
    msgState: false,
    // 
    msgType: "",
    // 
    msgContent: "",
    // 
    currentRequestPath: "",
    // 
    preRequestPath: "",
    // 
    currentRequestPathArr: [],
    // 
    menuState: false,
    // 
    menuTarget: "",
    // 
    menuPosition: { x: 0, y: 0 },
    // 
    menuType: "",
    // 
    menuRelativePath: "",
    // 
    menuShortName: "",
    // 
    renameState: false,
    // 
    renameRelativePath: "",
    // 
    copyFileState: false,
    // 
    copyShortName: "",
    // 
    copyFileRelativePath: "",
    // 
    copyFileAtDirRelativePath: "",
    // 
    isLoaded: false,
    // 
    editorItems: {},
    // 
    editableLength: 5000000,
    // 
    data: {},
    // 
    sortFlag: true,
    // 
    sortMethod: "byId",
  },
  applyMiddleware(
    (store) => (next) => (action) => {
      // 
      if ("isSamePath" in action) {
        next(action);
      } else {
        if (action.currentRequestPath === store.getState().currentRequestPath) {
          return next({ ...action, isSamePath: true });
        } else {
          if (action.currentRequestPath) {
            return next({ ...action, isSamePath: false });
          } else {
            return next({ ...action, isSamePath: true });
          }
        }
      }
    },
    (store) => (next) => (action) => {
      // 
      if (!action.isSamePath) {
        if (action.currentRequestPath.startsWith("/all")) {
          axiosPost("/all", { requestPath: action.currentRequestPath })
            .then((data) => {
              next({ ...action, data: data.state });
            })
            .catch((e) => {
              next({ ...action, data: {} });
            });
        } else {
          axiosPost("/recover", { requestPath: action.currentRequestPath })
            .then((data) => {
              next({ ...action, data: data.state });
            })
            .catch((e) => {
              next({ ...action, data: {} });
            });
        }
      } else {
        next(action);
      }
    },
    (store) => (next) => (action) => {
      // mime-type
      if (!action.isSamePath) {
        try {
          let newFiles = action.data.files.map((it) => {
            let fileTypeExtention = mime.lookup(it.shortPath) || "undefined";
            return {
              ...it,
              fileTypeExtention,
            };
          });
          action.data.files = newFiles;
          next(action);
        } catch (e) {
          next(action);
        }
      } else {
        next(action);
      }
    },
    (store) => (next) => (action) => {
      // linkTarget
      if (!action.isSamePath) {
        try {
          let newFiles = action.data.files.map((it) => {
            let linkTarget;
            if (it.fileType === "file") {
              let extention = it.fileTypeExtention.slice(
                0,
                it.fileTypeExtention.lastIndexOf("/")
              );
              if (it.fileTypeExtention === "text/html") {
                it["preview"] = true;
                it["linkPreview"] = `/file/${it.relativePath}/html/${it.id}`;
              }
              linkTarget = `/file/${it.relativePath}/${extention}/${it.id}`;
            } else {
              linkTarget = `/all/${it.relativePath}`;
            }
            return {
              ...it,
              linkTarget,
            };
          });
          action.data.files = newFiles;
          next(action);
        } catch (e) {
          next(action);
        }
      } else {
        next(action);
      }
    }
  )
);
